"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { 
  MapPin, Info, Copy, Check, ShoppingBag, 
  X, Instagram, Search, Package, Phone, LayoutDashboard, ChevronRight,
  BadgeCheck, Gem, Share2
} from "lucide-react";
import { Store } from "@/types";
import { useCart } from "@/context/CartContext";

const TiktokIcon = ({ size = 20 }: { size?: number }) => (
  <svg width={size} height={size} viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M9 12a4 4 0 1 0 4 4V4a5 5 0 0 0 5 5"/></svg>
);

const VerificationBadge = ({ store }: { store: any }) => {
  return (
    <div className="inline-flex items-center gap-1 ml-1.5 align-middle">
      {store.verification_status === 'verified' && (
        <span title="Verified Vendor">
          <BadgeCheck size={18} className="text-blue-500 fill-blue-50" />
        </span>
      )}
      {store.subscription_plan === 'diamond' && (
        <span title="Diamond Vendor">
          <Gem size={18} className="text-purple-600 fill-purple-50" />
        </span>
      )}
    </div>
  );
};

interface StoreFrontProps {
  store: Store;
  products: any[];
  categories: { id: string; name: string }[];
}

export default function StoreFront({ store, products, categories }: StoreFrontProps) {
  const { addToCart, cartCount, setIsCartOpen, isCartOpen } = useCart();
  
  const [activeCategory, setActiveCategory] = useState("All");
  const [searchTerm, setSearchTerm] = useState("");
  const [isInfoOpen, setIsInfoOpen] = useState(false);
  const [copied, setCopied] = useState(false);

  const handleCopyLink = () => {
    navigator.clipboard.writeText(`${window.location.origin}/${store.slug}`);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleAddToCart = (product: any) => {
    addToCart(product, store);
  };

  const filteredProducts = products.filter(product => {
    const matchesSearch = product.name.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = activeCategory === "All" || product.categories?.name === activeCategory;
    return matchesSearch && matchesCategory;
  });

  return (
    <div className="min-h-screen bg-white font-sans flex flex-col selection:bg-emerald-100">
      
      {/* --- PREMIUM GLASS NAVBAR --- */}
      <nav className="sticky top-0 z-40 bg-white/80 backdrop-blur-xl border-b border-gray-100 h-16 flex items-center justify-between px-4 md:px-8 shadow-sm">
         <div className="flex items-center gap-3">
            <Link href="/" className="flex items-center gap-1 group">
               <LayoutDashboard size={20} className="text-emerald-600"/> 
               <span className="font-extrabold text-gray-900 tracking-tight hidden md:block">StoreLink</span>
            </Link>
            <span className="text-gray-300 font-light text-xl">/</span>
            <div className="flex items-center gap-2">
               <div className="relative w-6 h-6 rounded-full overflow-hidden bg-gray-100 border border-gray-200">
                  {store.logo_url ? <Image src={store.logo_url} alt="" fill className="object-cover"/> : <div className="w-full h-full bg-gray-900"/>}
               </div>
               <h1 className="font-bold text-gray-900 truncate max-w-[150px] md:max-w-none flex items-center">
                 {store.name}
                 <VerificationBadge store={store} />
               </h1>
            </div>
         </div>

         <div className="flex items-center gap-2">
            <button onClick={() => setIsInfoOpen(true)} className="p-2 hover:bg-gray-100 rounded-full transition text-gray-600">
               <Info size={20} />
            </button>
         </div>
      </nav>

      {/* --- HERO SECTION --- */}
      <div className="relative w-full bg-gray-50 border-b border-gray-100">
         <div className="w-full h-48 md:h-64 relative overflow-hidden bg-gray-200">
            {store.cover_image_url ? (
               <Image src={store.cover_image_url} alt="Cover" fill className="object-cover" priority />
            ) : (
               <div className="absolute inset-0 flex items-center justify-center text-gray-300 bg-gray-100">
                  <Package size={48} className="opacity-20" />
               </div>
            )}
            <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
         </div>

         <div className="max-w-7xl mx-auto px-4 md:px-8 -mt-10 md:-mt-12 relative z-10 pb-8">
            <div className="flex flex-col md:flex-row items-start md:items-end justify-between gap-4">
               <div className="flex items-end gap-4">
                  <div className="w-24 h-24 md:w-32 md:h-32 rounded-2xl border-4 border-white shadow-lg bg-white relative overflow-hidden shrink-0">
                     {store.logo_url ? (
                        <Image src={store.logo_url} alt="Logo" fill className="object-cover" />
                     ) : (
                        <div className="flex items-center justify-center h-full text-2xl font-bold bg-gray-900 text-white">{store.name.charAt(0)}</div>
                     )}
                  </div>
                  
                  <div className="hidden md:block mb-2">
                     <h2 className="text-3xl font-extrabold text-gray-900 flex items-center gap-2 tracking-tighter uppercase">
                       {store.name}
                       <VerificationBadge store={store} />
                     </h2>
                     <p className="text-gray-500 flex items-center gap-1 text-sm mt-1 font-bold"><MapPin size={14} className="text-emerald-600"/> {store.location}</p>
                  </div>
               </div>

               <div className="flex gap-2 w-full md:w-auto mt-2 md:mt-0">
                  <button onClick={handleCopyLink} className="flex-1 md:flex-none py-3 px-6 bg-white border border-gray-200 shadow-sm rounded-2xl text-sm font-black text-gray-700 hover:border-gray-900 flex items-center justify-center gap-2 transition active:scale-95">
                     {copied ? <Check size={18} className="text-green-600"/> : <Share2 size={18}/>}
                     {copied ? "Copied" : "Share Store"}
                  </button>
                  <button onClick={() => setIsInfoOpen(true)} className="flex-1 md:flex-none py-3 px-6 bg-gray-900 shadow-xl rounded-2xl text-sm font-black text-white hover:bg-gray-800 flex items-center justify-center gap-2 transition active:scale-95">
                     More Info <ChevronRight size={18} />
                  </button>
               </div>
            </div>

            <div className="mt-4 md:hidden">
               <h2 className="text-2xl font-extrabold text-gray-900 flex items-center gap-2 tracking-tighter uppercase">
                 {store.name}
                 <VerificationBadge store={store} />
               </h2>
               <p className="text-gray-500 text-sm mt-1 font-medium leading-relaxed">{store.description || "Welcome to our digital storefront."}</p>
               <div className="flex items-center gap-4 mt-3 text-[10px] font-black uppercase tracking-widest text-gray-400">
                  <span className="flex items-center gap-1 bg-white px-2 py-1 rounded-md shadow-sm border border-gray-100"><MapPin size={12} className="text-emerald-500"/> {store.location}</span>
                  <span className="bg-white px-2 py-1 rounded-md shadow-sm border border-gray-100">{products.length} Products</span>
               </div>
            </div>
         </div>
      </div>

      {/* --- SEARCH & CATEGORY BAR (Swipeable for Mobile) --- */}
      <div className="sticky top-16 z-30 bg-white/95 backdrop-blur-md border-b border-gray-100 shadow-sm">
         <div className="max-w-7xl mx-auto px-4 md:px-8 py-3">
            <div className="flex flex-col md:flex-row gap-3">
               <div className="relative w-full md:max-w-xs">
                  <Search className="absolute left-3.5 top-3 text-gray-400" size={18} />
                  <input 
                    className="w-full pl-11 pr-4 py-2.5 bg-gray-100 rounded-xl text-sm outline-none focus:ring-2 focus:ring-gray-900 focus:bg-white transition font-medium"
                    placeholder="Search collection..."
                    value={searchTerm}
                    onChange={e => setSearchTerm(e.target.value)}
                  />
               </div>
               
               <div className="flex gap-2 overflow-x-auto no-scrollbar items-center pb-1 md:pb-0">
                  <button 
                    onClick={() => setActiveCategory("All")}
                    className={`px-5 py-2 rounded-xl text-xs font-black uppercase tracking-widest whitespace-nowrap transition ${activeCategory === "All" ? "bg-black text-white shadow-lg" : "bg-white text-gray-500 border border-gray-200"}`}
                  >
                    All Items
                  </button>
                  {categories.map(cat => (
                    <button 
                      key={cat.id}
                      onClick={() => setActiveCategory(cat.name)}
                      className={`px-5 py-2 rounded-xl text-xs font-black uppercase tracking-widest whitespace-nowrap transition ${activeCategory === cat.name ? "bg-black text-white shadow-lg" : "bg-white text-gray-500 border border-gray-200"}`}
                    >
                      {cat.name}
                    </button>
                  ))}
               </div>
            </div>
         </div>
      </div>

      {/* --- PRODUCT GRID --- */}
      <div className="flex-1 bg-white">
         <div className="max-w-7xl mx-auto px-4 md:px-8 py-8">
            {filteredProducts.length === 0 ? (
               <div className="text-center py-20 flex flex-col items-center">
                  <div className="w-20 h-20 bg-gray-50 rounded-[2rem] flex items-center justify-center mb-4 border border-gray-100"><Package size={32} className="text-gray-200"/></div>
                  <h3 className="text-lg font-black text-gray-900 uppercase tracking-tight">No products found</h3>
                  <p className="text-gray-400 text-sm">Try a different search or category.</p>
               </div>
            ) : (
               <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 md:gap-8">
                  {filteredProducts.map(product => (
                    <div key={product.id} className="group bg-white rounded-[1.5rem] overflow-hidden border border-gray-100 shadow-sm hover:shadow-xl transition-all duration-500 flex flex-col relative">
                       <Link href={`/product/${product.id}`} className="block relative aspect-[4/5] bg-gray-50 overflow-hidden">
                          {product.image_urls?.[0] ? (
                            <Image 
                              src={product.image_urls[0]} 
                              alt={product.name} 
                              fill 
                              className="object-cover transition-transform duration-700 md:group-hover:scale-105" 
                            />
                          ) : (
                            <div className="flex items-center justify-center h-full text-gray-200"><Package size={40}/></div>
                          )}
                          
                          {/* Floating Price Tag */}
                          <div className="absolute bottom-3 left-3 bg-white/90 backdrop-blur-md px-3 py-1 rounded-xl shadow-sm border border-white/20">
                             <p className="text-xs font-black text-gray-900 tracking-tighter">₦{product.price.toLocaleString()}</p>
                          </div>

                          {product.stock_quantity === 0 && (
                             <div className="absolute inset-0 bg-black/40 backdrop-blur-[1px] flex items-center justify-center">
                                <span className="bg-white text-black text-[9px] font-black px-3 py-1.5 rounded-lg uppercase tracking-widest shadow-2xl">Sold Out</span>
                             </div>
                          )}
                       </Link>

                       <div className="p-4 flex flex-col flex-1">
                          <div className="mb-3">
                             <p className="text-[10px] text-emerald-600 font-black uppercase tracking-widest mb-1">{product.categories?.name || "General"}</p>
                             <h3 className="font-bold text-gray-900 leading-tight line-clamp-2 text-sm md:text-base h-10">{product.name}</h3>
                          </div>
                          
                          <button 
                            disabled={product.stock_quantity === 0}
                            onClick={() => handleAddToCart(product)}
                            className="mt-auto w-full py-3 bg-gray-50 md:group-hover:bg-emerald-600 md:group-hover:text-white text-gray-900 rounded-2xl flex items-center justify-center gap-2 transition-all duration-300 font-black text-[10px] uppercase tracking-widest disabled:opacity-50 active:scale-95"
                          >
                             <ShoppingBag size={14} /> Add to Cart
                          </button>
                       </div>
                    </div>
                  ))}
               </div>
            )}
         </div>
      </div>

      {/* --- FOOTER --- */}
      <footer className="bg-gray-50 border-t border-gray-100 py-10 text-center mt-auto">
          <Link href="/" className="inline-flex items-center gap-2 opacity-40 hover:opacity-100 transition-opacity">
             <LayoutDashboard size={16} className="text-emerald-600"/>
             <span className="font-black text-gray-900 uppercase tracking-tighter text-xs">StoreLink social engine</span>
          </Link>
          <p className="text-[9px] text-gray-400 font-bold uppercase tracking-[0.2em] mt-3">Verified Social Commerce © 2025</p>
      </footer>

      {/* --- FLOATING CART --- */}
      {cartCount > 0 && !isCartOpen && (
        <button 
          onClick={() => setIsCartOpen(true)} 
          className="fixed bottom-6 right-6 bg-gray-900 text-white p-4 rounded-[1.5rem] shadow-[0_20px_50px_rgba(0,0,0,0.2)] z-50 hover:scale-110 active:scale-90 transition-all animate-in zoom-in"
        >
           <ShoppingBag size={24} />
           <span className="absolute -top-1 -right-1 bg-emerald-500 w-6 h-6 rounded-full flex items-center justify-center text-[10px] font-black border-2 border-white">
             {cartCount}
           </span>
        </button>
      )}

      {/* --- INFO PANEL --- */}
      {isInfoOpen && (
         <div className="fixed inset-0 z-[60] bg-black/60 backdrop-blur-sm flex justify-end animate-in fade-in">
            <div className="absolute inset-0" onClick={() => setIsInfoOpen(false)}></div>
            <div className="relative w-full max-w-sm bg-white h-full shadow-2xl p-8 overflow-y-auto animate-in slide-in-from-right duration-300 flex flex-col rounded-l-[2.5rem]">
               <div className="flex justify-between items-center mb-8">
                  <h2 className="font-black text-xl text-gray-900 uppercase tracking-tighter">About Business</h2>
                  <button onClick={() => setIsInfoOpen(false)} className="p-2 bg-gray-100 rounded-full"><X size={20}/></button>
               </div>

               <div className="text-center mb-8">
                  <div className="w-24 h-24 rounded-2xl mx-auto mb-4 overflow-hidden border-2 border-gray-100 relative shadow-inner">
                      {store.logo_url ? <Image src={store.logo_url} alt="" fill className="object-cover" /> : null}
                  </div>
                  <h3 className="font-black text-2xl text-gray-900 tracking-tighter uppercase">
                    {store.name}
                    <VerificationBadge store={store} />
                  </h3>
                  <p className="text-gray-400 text-xs font-bold uppercase mt-1 tracking-widest">{store.location}</p>
               </div>

               <div className="bg-gray-50 p-6 rounded-2xl mb-8 border border-gray-100">
                  <p className="text-gray-600 text-sm leading-relaxed italic text-center font-medium">
                     "{store.description || 'Welcome to our store! We sell amazing products.'}"
                  </p>
               </div>

               <div className="space-y-3 mt-auto">
                   <h4 className="font-black text-gray-400 text-[10px] uppercase tracking-widest mb-2">Social Connect</h4>
                   {store.whatsapp_number && (
                       <div className="flex items-center gap-4 p-4 rounded-xl border border-gray-100 bg-emerald-50/30">
                           <div className="bg-emerald-500 p-2 rounded-lg text-white"><Phone size={18}/></div>
                           <div>
                               <p className="text-[9px] text-emerald-600 font-black uppercase tracking-widest">WhatsApp Business</p>
                               <p className="text-sm font-bold text-gray-900">{store.whatsapp_number}</p>
                           </div>
                       </div>
                   )}
                   {store.instagram_handle && (
                       <a href={`https://instagram.com/${store.instagram_handle}`} target="_blank" className="flex items-center justify-between p-4 bg-gray-50 rounded-xl font-bold hover:bg-pink-50 hover:text-pink-600 transition">
                           <span className="flex items-center gap-3 text-xs uppercase tracking-widest"><Instagram size={18} /> Instagram</span>
                           <ChevronRight size={14}/>
                       </a>
                   )}
               </div>
            </div>
         </div>
      )}
    </div>
  );
}